import { JSX } from 'preact';
import { classNames } from '../utils/UNSAFE_classNames';
import { cssProps } from '@oracle/oraclejet-cssprops';

type IntrinsicProps = Pick<JSX.HTMLAttributes<HTMLDivElement>, 'children'>;
type Props = {
  /**
   * Specifies the source for the image of the avatar.  Will only be displayed if no icon component is specified as a child.  Image will be rendered as a
   * background image.
   */
  src?: string;
  /**
   * Specifies the initials of the avatar.  Will only be displayed if both the src attribute and the child icon component are not specified.
   */
  initials?: string;
  /**
   * Specifies the background of the avatar.
   */
  background?:
    | 'neutral'
    | 'orange'
    | 'green'
    | 'teal'
    | 'blue'
    | 'slate'
    | 'pink'
    | 'purple'
    | 'lilac'
    | 'gray';

  /**
   * Specifies the size of the avatar.
   */
  size?: '2xs' | 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl';

  /**
   * Specifies the shape of the avatar. Can be square or circle.  The default value of this
   * property varies by theme.
   */
  shape?: 'square' | 'circle';
  /**
   * Avatar will set the aria-label attribute with accessibleLabel.
   * If the component needs to be accessible, the aria label of the avatar must be populated.
   * The component does not need to be accessible if any associated information (e.g. the name of the person represented by the avatar)
   * is already available to assistive technologies (e.g when the name is rendered in addition to the avatar as part of the page content).
   *
   *  If accessibleLabel  is set, role will internally be set to 'img'.
   */
  accessibleLabel?: string;
} & IntrinsicProps;

export function Avatar({ children, src, ...otherProps }: Props) {
  if (src && !children) {
    return <ImageAvatar src={src} {...otherProps}></ImageAvatar>;
  } else {
    return <ContentAvatar {...otherProps}>{children}</ContentAvatar>;
  }
}

const ImageAvatar = ({ src, ...otherProps }: Props) => {
  const classes = classNames([contentStyles.base, contentStyles.image]);
  return (
    <AvatarWrapper {...otherProps}>
      <div class={classes} style={{ backgroundImage: `url(${src})` }}></div>
    </AvatarWrapper>
  );
};

const ContentAvatar = ({ children, initials, ...otherProps }: Props) => {
  const classes = [
    contentStyles.base,
    contentStyles.content,
    initials ? contentStyles.initials : contentStyles.icon
  ];

  const childContent = initials || children;
  return (
    <AvatarWrapper {...otherProps}>
      <div aria-hidden="true" class={classNames(classes)}>
        {childContent}
      </div>
    </AvatarWrapper>
  );
};

const AvatarWrapper = ({
  background = 'neutral',
  size = 'md',
  shape = 'square',
  children,
  accessibleLabel
}: Props) => {
  const classes = classNames([
    styles.base,
    `oj-c-avatar-bg-${background}`,
    `oj-c-avatar-${size}`,
    shape === 'circle' && styles.circle
  ]);
  if (accessibleLabel) {
    return (
      <div aria-label={accessibleLabel} role="img" class={classes}>
        {children}
      </div>
    );
  } else {
    return <div class={classes}>{children}</div>;
  }
};

const styles = {
  base: cssProps`${{
    display: 'inline-grid',
    height: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-size)',
    width: 'var(--oj-c-PRIVATE-DO-NOT-USE--avatar-size)'
  }}`,

  circle: cssProps`${{
    '--oj-c-PRIVATE-DO-NOT-USE-avatar-border-radius': '50%'
  }}`
};

const contentStyles = {
  base: cssProps`${{
    borderRadius: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-border-radius)',
    alignSelf: 'stretch',
    alignItems: 'center',
    justifyContent: 'center',
    display: 'flex',
    height: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-size)',
    width: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-size)',
    gridRowStart: '1',
    gridColumnStart: '1',
    gridRowEnd: '2',
    gridColumnEnd: '2',
    backgroundPosition: 'center center',
    overflowX: 'hidden',
    overflowY: 'hidden'
  }}`,
  content: cssProps`${{
    backgroundColor: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-bg-color)',
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-text-color)',
    backgroundSize: '196px 196px',
    backgroundImage: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-pattern)'
  }}`,
  image: cssProps`${{
    backgroundSize: 'cover'
  }}`,
  initials: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-initials-font-size)',
    fontWeight: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-initials-font-weight)'
  }}`,
  icon: cssProps`${{
    fontSize: 'var(--oj-c-PRIVATE-DO-NOT-USE-avatar-icon-font-size)'
  }}`
};
